@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Allison&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  margin: 0%;
  font-family: "Poppins", sans-serif;
}

#mainPage {
  height: 100vh;
}

#HomeTop {
  height: 10%;
}

#HomeMain {
  -ms-scroll-snap-type: y mandatory;
      scroll-snap-type: y mandatory;
  overflow-y: scroll;
  height: 90%;
  align-items: center;
}

@media only screen and (max-width: 768px) {
  #HomeMain {
    height: 75%;
  }
}

#HomeMain section {
  height: 100%;
  width: 100%;
  scroll-snap-align: start;
  overflow: hidden;
  align-items: center;
}

#TopNav {
  position: absolute;
  top: 0;
  z-index: 1;
  background-color: rgb(45, 45, 45);
  height: 10vh;
  width: 100%;
  justify-content: space-evenly;
  display: flex;
  align-items: center;
}

#TopNav img {
  object-fit: contain;
  max-width: 115px;
}

#TopNav > div {
  display: flex;
}

#TopNav .links a {
  text-decoration: none;
  color: white;
  font-size: medium;
  padding-left: 5px;
  padding-right: 5px;
}
#TopNav .social a {
  text-decoration: none;
  color: white;
  font-size: larger;
  padding-left: 5px;
  padding-right: 5px;
}
#TopNav a:hover {
  color: cyan;
  text-shadow: 0 0 3px black, 0 0 3px black;
}

@media only screen and (max-width: 768px) {
  #TopNav .links {
    display: none;
  }
}

#Homepage {
  display: flex;
  align-items: center;
  justify-content: space-around;
  height: 100%;
  padding-left: 05%;
  padding-right: 05%;
}

.homeImg {
  width: 300px;
}

.homeText {
  vertical-align: middle;
}
.homeText h1 {
  font-family: "Allison", cursive;
  padding-right: 30px;
  font-size: 6rem;
  text-align: end;
  line-height: 60px;
  margin-bottom: 50px;
}

#Intropage {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  height: 100%;
  padding-left: 05%;
  padding-right: 05%;
}

.introText {
  width: 40%;
}

@media only screen and (max-width: 768px) {
  .introText {
    width: 60%;
  }
  .introText p {
    font-size: 0.7rem;
  }
}

.sh1 {
  line-height: 15px;
}
.sp1 {
  padding: 2%;
  background-color: rgba(255, 193, 219, 0.9);
  border-radius: 10px;
}
.sh2 {
  color: rgba(255, 193, 219, 1);
  text-shadow: 0 0 2px black, 0 0 2px black, 0 0 2px black;
}

h2 {
  font-size: larger;
}
p {
  font-size: medium;
}

.introImg {
  overflow: hidden;
  border-radius: 10px;
  object-fit: contain;
  align-items: center;
  width: 35%;
  height: 45%;
}
.introImg img {
  object-fit: cover;
  width: 100%;
  height: 100%;
}

#Teampage {
  height: 100%;
  padding-left: 06%;
  padding-right: 06%;
  padding-top: 5%;
}

#Teampage .row {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
}
#Teampage .team.row._1 .teamMainImg {
  width: 33vh;
  height: 33vh;
}
#Teampage .team.row._1 img {
  border-radius: 10px;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
#Teampage .team.row._1 .teamIntro {
  width: 40%;
}
#Teampage .team.row._1 .p2 {
  font-style: italic;
  font-weight: bold;
  margin-top: 25px;
  margin-right: 50px;
}

#Teampage .team.row._2 {
  padding-top: 01%;
}
#Teampage .team.row._2 .memImgCont {
  height: 20vh;
  width: 20vh;
  vertical-align: middle;
  align-items: center;
  align-content: center;
  border-radius: 50%;
  overflow: hidden;
}
#Teampage .team.row._2 .mem h2,
#Teampage .team.row._2 .mem p {
  margin-top: 01%;
  margin-bottom: 01%;
  max-width: 28vw;
}
#Teampage .team.row._2 .memImgCont img {
  height: 100%;
  width: 100%;
  object-fit: cover;
}

@media only screen and (max-width: 768px) {
  #Teampage {
    padding-left: 03%;
    padding-right: 03%;
  }
  #Teampage .team.row._1 .p2 {
    margin-top: 10px;
    margin-right: 15px;
  }
  #Teampage .team.row._1 .teamMainImg {
    height: 22vh;
    width: 22vh;
  }
  #Teampage .team.row p {
    font-size: 0.65rem;
  }
  #Teampage .team.row._2 {
    margin-top: 5%;
  }
  #Teampage .team.row._2 .memImgCont {
    height: 15vh;
    width: 15vh;
  }
  #Teampage .team.row._2 .mem h2,
  #Teampage .team.row._2 .mem p {
    max-width: 38vw;
  }
}

#ContactDiv {
  height: 100%;
  padding-top: 3%;
  padding-left: 02%;
  padding-right: 02%;
  background-image: url(/spfc/static/media/end.dffeb24a.png);
  background-repeat: no-repeat;
  background-position: right;
  background-size: cover;
  text-shadow: 0 0 6px white, 0 0 5px white, 0 0 4px white, 0 0 3px white;
}

#ContactDiv h1 {
  font-family: "Allison";
  font-size: 4rem;
}
#ContactDiv label {
  margin-bottom: 01%;
  font-weight: bold;
}
#ContactDiv input,
#ContactDiv textarea {
  margin-top: 1%;
  border-radius: 10px;
  padding: 1%;
}
#ContactDiv button {
  padding: 01%;
  width: 80px;
  height: 35px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgb(45, 45, 45);
  color: white;
  border-radius: 10px;
}

.ContactMain {
  display: flex;
  position: relative;
  justify-content: center;
  padding-top: 03.5%;
}

.ContactInfo {
  margin-right: 10%;
  -webkit-transform: translate(0%, 22.5%);
  transform: translate(0%, 22.5%);
}

.ContactStart {
  display: flex;
  justify-content: center;
}

.ContactStart > div > * {
  display: flex;
  justify-content: center;
}

.ContactFormDiv {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 30%;
}

.btn {
  display: flex;
  float: right;
}

.mb-3 {
  margin-bottom: 3%;
}

.form-control,
form {
  width: 100%;
}

@media only screen and (max-width: 768px) {
  #ContactDiv p {
    font-size: 0.7rem;
  }
  .ContactInfo {
    margin-right: 02%;
    width: 120px;
  }
  .ContactFormDiv {
    width: 55%;
  }
}

